.hero h1 {
    font-family: 'Squealer', sans-serif;
    font-size: 5rem;
    color: #fff;
}

.hero h2 {
    font-family: 'Squealer', sans-serif;
    font-size: 3rem;
    color: #fff;
}

.hero p {
    font-size: 1.5rem;
    margin-top: 10px;
}

/*video in autoplay*/
.hero {
  position: relative;
  height: 100vh;
  overflow: hidden;
  display: flex;
  align-items: center;
  justify-content: center;
  text-align: center;
  color: white;
  background: rgba(0, 0, 0, 0.7);
}

/* Contenitore video */
.video-background iframe {
  position: absolute;
  top: 50%;
  left: 50%;
  height: 100vh; /* sempre piena altezza */
  width: calc(100vh * 16 / 9); /* mantiene rapporto 16:9 */
  min-width: 100%; /* non lascia mai spazio ai lati */
  min-height: 100%; /* fallback */
  transform: translate(-50%, -50%);
  pointer-events: none;
  transition: all 0.3s ease-in-out;
  
}

/* Video in modalità “cover” */
.video-background iframe {
  position: absolute;
  top: 50%;
  left: 50%;
  width: 120%;
  height: 120%;
  transform: translate(-50%, -50%);
  pointer-events: none;
  transition: all 0.3s ease-in-out;
}

/* Contenuto sopra il video */
.hero-content {
  position: relative;
  z-index: 1;
  text-shadow: 0 0 20px rgba(0, 0, 0, 0.7);
  
}

/* Overlay opzionale per contrasto */
.hero::before {
  content: '';
  position: absolute;
  inset: 0;
  background: rgba(0, 0, 0, 0.35);
  z-index: 0;
}

/*  Mobile fix */
@media (max-width: 768px) {
  .video-background iframe {
    width: 220%;
    height: 220%;
  }
}

.shadow-hover {
    transition: box-shadow 0.3s ease;
    box-shadow: 0 2px 5px rgba(0, 0, 0, 0.4);
}

.shadow-hover:hover {
    box-shadow: 0 10px 25px rgba(0, 0, 0, 0.5);
    cursor: pointer;
}

.cards-wrapper {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-evenly;
}

.card-item {
    flex: 0 1 calc(33.333% - 20px);
    /* 3 per riga, con un po' di margine */
    margin: 10px;
}

@media (max-width: 992px) {
    .card-item {
        flex: 0 1 calc(50% - 20px);
        /* 2 per riga */
    }
}

@media (max-width: 576px) {
    .card-item {
        flex: 0 1 calc(100% - 20px);
        /* 1 per riga */
    }
}


.card {
    overflow: hidden;
    /* nasconde le parti che escono dell'immagine */
}

.card-img {
    width: 100%;
    height: auto;
    object-fit: cover;
    transition: transform 0.3s ease;
    display: block;
}

.card:hover .card-img {
    transform: scale(1.05);
    /* zoom interno */
}

.card-item>a {
  text-decoration: none;
}